.dash--navbar {
    background-color: #060b26;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
    z-index: 0;
}

.dash--menu-bars {
    margin-left: 2rem;
    font-size: 2rem;
    background: none;
}

.dash--nav-menu {
    background-color: #060b26;
    width: 250px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 10;
    left: -100%;
    transition: 850ms;
    z-index: 1
}


.dash--nav-menu.active{
    left: 0;
    transition: 350ms;
    z-index: 1
}

.dash--nav-text{
    display: flex;
    justify-content: start;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px;
}

.dash--nav-text a{
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0px 16px;
    border-radius: 4px;
}

.dash--nav-text a:hover {
    background-color: #1a83ff;

}

.dash--nav-menu-items {
    width: 100%;

}

.dash--navbar-toggle {
    background-color: #060b26;
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
}

span {
    margin-left: 16px;
}